* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans";
  font-size: 10px; }

#wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 5rem; }

.unselectable {
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none; }

.container {
  display: flex;
  position: fixed;
  height: 100%;
  flex-direction: column;
  padding: 25px;
  padding-bottom: 0; }

.division {
  padding: 25px;
  width: 400px;
  background-color: #32393d;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin-bottom: 25px; }

.growdiv {
  flex-grow: 100; }

.navHeading {
  color: white;
  font-size: 37px;
  margin-left: 10px; }

.sidebar {
  width: 100%; }
  .sidebarEl {
    padding: 0.2rem 0;
    font-size: 16px;
    color: white;
    margin: 0.2rem 0;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1e2024;
      cursor: pointer;
      color: #f2f1ef;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 25px;
    margin: 10px 25px;
    color: white; }

.user {
  display: flex;
  flex-basis: row;
  vertical-align: middle;
  margin: 0 0 15px 0; }

.ppicture {
  height: 70px;
  width: 70px;
  border-radius: 35px;
  margin-right: 15px;
  object-fit: cover; }

.username {
  display: block;
  color: white;
  font-size: 28px;
  line-height: 37px; }

.activity {
  display: block;
  color: #C6C6D9;
  font-size: 16px;
  line-height: 27px; }

.online {
  position: absolute;
  top: 70px;
  left: 70px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #66bb6a;
  border: solid #32393d 7px; }

.friend {
  position: relative; }

.fppicture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover; }

.fonline {
  position: absolute;
  top: 25px;
  left: 25px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: solid #32393d 7px; }

.friend {
  display: flex;
  flex-basis: row;
  vertical-align: middle;
  margin: 10px 0; }

.fusername {
  display: block;
  color: white;
  font-size: 21px;
  line-height: 25px; }

.factivity {
  display: block;
  color: #C6C6D9;
  font-size: 16px;
  line-height: 25px; }

.fabout {
  margin-left: 10px; }

.yellow {
  background-color: #ffec40; }

.red {
  background-color: #ff3131; }

.green {
  background-color: #66bb6a; }

.black {
  background-color: #1e2024; }
